import $$ from "cmn-utils";
import axios from "axios";

import routerLinks from "@/utils/routerLinks";
import { refresh_AUTH } from '@/routes/Auth/Login/service';
let checkAuth;

export default (user, history, check) => {
  if (check) clearInterval(checkAuth)
  if (user) {
    const time = new Date().getTime();
    if (time >= user.expires_in) {
      history.push(routerLinks("Login"))
    } else {
      axios.defaults.headers.common['Authorization'] = user.token;
      checkAuth =  setInterval(async () => {
        if (user.remember) {
          const { data } = await refresh_AUTH();
          axios.defaults.headers.common['Authorization'] = data.token;
          data.remember = user.remember
          $$.setStore('user', data);
        } else {
          history.push(routerLinks("Login"))
          clearInterval(checkAuth);
        }
      }, user.expires_in - time - 3000)
    }
  }
}
