import intl from 'react-intl-universal';
import $$ from 'cmn-utils';

import modelEnhance from '@/utils/modelEnhance';
import routerLinks from "@/utils/routerLinks";

import messages from './messages';
import axios from "axios";

export default modelEnhance({
  namespace: 'global',

  state: {
    menu: [],
    flatMenu: [],
    locale: $$.getStore('language', "en"),
  },

  effects: {
    *getMenu({payload}, { put }) {
      const data = [
        { name: intl.formatMessage(messages.order), icon: 'las la-shopping-cart', path: routerLinks("Order", "curd"),},
        { name: intl.formatMessage(messages.product), icon: 'las la-box-open', path: routerLinks("Product", "curd"),},
        { name: intl.formatMessage(messages.discount), icon: 'las la-percent', path: routerLinks("Discount", "curd"),},
        { name: intl.formatMessage(messages.page), icon: 'las la-book-open', path: routerLinks("Page", "curd"),}
        //  generate link to here
      ];

      const loopMenu = (menu, pitem = {}) => {
        menu.forEach(item => {
          if (pitem.path) {
            item.parentPath = pitem.parentPath ? pitem.parentPath.concat(pitem.path) : [pitem.path];
          }
          if (item.children && item.children.length) {
            loopMenu(item.children, item);
          }
        });
      }
      loopMenu(data);

      yield put({
        type: 'getMenuSuccess',
        payload: data,
      });
    },
    *setLocale({payload}, {put}) {
      axios.defaults.headers.common['X-localization'] = payload;
      yield put({
        type: 'setLocaleSuccess',
        payload,
      });
    }
  },

  reducers: {
    getMenuSuccess(state, { payload }) {
      return {
        ...state,
        menu: payload,
        flatMenu: getFlatMenu(payload),
      };
    },
    setLocaleSuccess(state, { payload }) {
      $$.setStore('language', payload);
      return {
        ...state,
        locale: payload,
      };
    },
  },
});

export function getFlatMenu(menus) {
  let menu = [];
  menus.forEach(item => {
    if (item.children) {
      menu = menu.concat(getFlatMenu(item.children));
    }
    menu.push(item);
  });
  return menu;
}
